import Master from '../componentes/Master';
import fetch from 'isomorphic-unfetch';
import Precio from '../componentes/Precio';


const index = (props) => (
    <Master>
        <div className="row">
            <div className="col-12">
                <h2>Precio del Bitcoin</h2>
                <Precio 
                    precio = {props.precioBitcoin}
                />
            </div>
            <div className="col-md-8">
                <h2>Noticias del Bitcoin</h2>
                <Noticias 
                    noticias = {props.noticias}
                />
            </div>
            <div className="col-md-4">
                <h2>Proximos eventos del Bitcoin</h2>
            </div>
        </div>
    </Master>
);

index.getInitialProps = async () => {

    const urlBitcoin = `https://api.coinmarketcap.com/v2/ticker/1/`;
    const precio = await fetch(urlBitcoin);
    const resPrecio = await precio.json();

    const urlNoticias = `https://newsapi.org/v2/everything?q=bitcoin&sortBy=publishedAt&apiKey=aa5536b31cbf4bf1b5cf4a96bc8938c2&language=es`;
    const noticias = await fetch(urlNoticias);
    const resNoticias = await noticias.json();

    return {
        precioBitcoin: resPrecio.data.quotes.USD,
        noticias: resNoticias.articles
    }

}

export default index;